.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
}


.line {
  height: 20px;
  background-color: black;
}

/* Header */

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 960px;
  height: 110px;
  margin: 0 auto;
  padding: 0 10px;
}

.header span {
  color: #ff6800;
}

.header ul {
  display: flex;
  list-style: none;
}

.header li {
  padding-left: 24px;
}

.nav a {
 text-decoration: none;
 color: black;
}

.nav a:hover {
  color: #ff6800;
 }

 /* About Section */

.about {
  height: 693px;
  background: no-repeat center url(https://img.freepik.com/free-photo/set-of-tools-in-a-tool-kit-isolated_1303-20319.jpg?w=1380&t=st=1691580665~exp=1691581265~hmac=a40394db8014f52ac18277af186854f8dbb9868798a9978aed5d6b8c0851522a);
  background-size: 100%;
  background-position: 1% 35%;
}

.about_wrapper {
  padding-top: 26px;
  color: white;
  width: 960px;
  text-align: center;
  margin: 0 auto;
}

.about_wrapper  p {
  font-size: 20px;
  margin: 0 0 10px 0;
}
.about_wrapper b {
  color: #c46004;
}

.about_wrapper h2 {
  font-size: 32px;
  margin: 0;
}

.about_wrapper img {
  width:400px;
  height: 340px;
  margin-top: 40px;
  background: none;
}

.about_btn {
  background: #fff;
  border-radius: 36px;
  height: 72px;
  width: 215px;
  margin: 0 auto;
  color: red;
  text-align: center;
  margin-top: 20px;
}

.about_btn p {
  padding-top: 15px;
  font-size: 1.7em;
}

.about_prozent {
  background: rgb(226, 0, 21);
  background: -moz-linear-gradient(left, rgba(226, 0, 21, 1) 0%, rgba(252, 1, 24, 1) 100%);
  background: -webkit-linear-gradient(left, rgb(231 27 0) 0%, rgb(252 125 1) 100%);
  background: linear-gradient(to right, rgb(231 27 0) 0%, rgb(252 125 1) 100%);
  width: 450px;
  margin: 0 auto;
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  line-height: normal;
  text-align: center;
  padding: 7px 15px;
  position: relative;
  top: 20px;
  border-radius: 20px;
}


.about_wrapper span {
  display: block;
  font-size: 16px;
  margin: 3px auto;
  animation: sales 1.5s ease-in-out infinite;
  transition: all 1.5s;
  max-width: 300px;
  overflow: hidden;
}

@keyframes sales {
  0% {
      transform: scale(1);
  } 
  100% {
      transform: scale(1.2);
  }
}

/* Order */

.order {
  width: 600px;
  margin: 0 auto;
  margin-top: 80px;
}

.ordet_btn {
  position: relative;
  display: block;
  width: 300px;
  height: 72px;
  line-height: 72px;
  letter-spacing: .5px;
  border-radius: 36px;
  margin: 0 auto;
  border: none;
  text-align: center;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  color: #fff;
  background: rgb(226, 0, 21);
  background: -moz-linear-gradient(left, rgba(226, 0, 21, 1) 0%, rgba(252, 1, 24, 1) 100%);
  background: -webkit-linear-gradient(left, rgb(231 27 0) 0%, rgb(252 125 1) 100%);
  background: linear-gradient(to right, rgb(231 27 0) 0%, rgb(252 125 1) 100%);
}

.ordet_btn p {
  font-size: 20px;
  color: white;
}

.order a {
  text-decoration: none;
}

/* Tools */

.tool {
  width: 944px;
  margin: 30px auto 0 auto;
}

.tools_title {
  margin: 0 0 50px 0;
  text-align: center;
  font-size: 24px;

}

.tools_title span {
  color:#c46004;
  font-size: 31px;
}

.tools_title b {
  color:#c46004

}


.tools_about {
  text-align: center;
  margin: -20px 0 50px;
}

.tools_mus {
  width: 264px;
  text-align: center;
  font-size: 16px;
  font-weight: 700
}

.tools_mus img {
  border-radius: 4px;
  margin-bottom: 20px;
  width: 264px;
  height: 182px;
}

.items {
  width: 944px;
  display: flex;
  justify-content: space-between;
}

/* order_tools */

.order_tools {
  width: 944px;
  margin: 0 auto;
  margin-top: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.order_list ul {
  margin: 0px;
  padding: 0px;
}

.order_tools img {
  width: 396px;
  height: 360px;
}

.order_list li {
  width: 344px;
  color:#214133;
  list-style-type: none;
  font-size: 15px;
  padding: 0 0 25px 30px;
  line-height: 1.4em;
}

.order_list li::before {
  content: "ðŸ—¸";
  margin-right: 10px;
  color:#c46004;
}

.order_btn {
  width: 300px;
  height: 72px;
  background: rgb(226, 0, 21);
  background: -moz-linear-gradient(left, rgba(226, 0, 21, 1) 0%, rgba(252, 1, 24, 1) 100%);
  background: -webkit-linear-gradient(left, rgb(231 27 0) 0%, rgb(252 125 1) 100%);
  background: linear-gradient(to right, rgb(231 27 0) 0%, rgb(252 125 1) 100%);
  border-radius: 32px;
  text-align: center;
  margin: 0 auto;
}
 a {
  text-decoration: none;
}

.order_btn p {
  font-size: 20px;
  color: white;
  padding-top: 20px;
}

/* included */

.included h2{
  margin: 50px 0 50px 0;
  text-align: center;
  font-size: 31px;
  color:#c46004;
}
.included p {
  text-align: center;
  margin: -20px 0 50px;
}

.included_video  {
  width: 940px;
  height: 529px;
  margin: 0 auto;
}


/* Specifications */

.product {
  background: #f4f4f4 no-repeat top center;
  height: 600px;
  margin-top: 80px;
}

.product_title h2 {
  margin: 45px 0 50px 0;
  padding-top: 40px;
  font-size: 31px;
  color:#c46004;
  text-align: center;
}

tbody {
  display: table-row-group;
  vertical-align: middle;
  border-color: inherit;
  width: 500px;
  margin-right: 50px;
}

.product_list {
  display: flex;
  justify-content: center;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.product_photo img {
  width: 349px;
  height: 342px;
}

.table tr:nth-child(odd) td {
  background: #fff;
}

.table tr:nth-child(odd) td:nth-child(odd) {
  border-radius: 5px 0 0 5px;
}

.table tr:nth-child(odd) td:nth-child(even) {
  border-radius: 0 5px 5px 0;
}

.table tr td:nth-child(odd) {
  width: 55%;
  padding: 8px 0 8px 15px;
}

.table tr td:nth-child(even) {
  width: 45%;
  padding: 8px 15px 8px 0px;
}

/* Feedback */

.Feedback {
  background: url(./img/pat.jpg) no-repeat center top / cover;
  height: 720px;
  padding-top: 50px;
}
.user_img img {
  width: 39px;
  height: 39px;
}

.Feedback_title h2 {
  color: #fff;
  margin-bottom: 30px;
  text-align: center;
  font-size: 31px;
}

.user {
  width: 940px;
  height: 137px;
  background-color: #fff;
  margin: 0 auto;
  margin-bottom: 15px;
  border-radius: 5px;
  padding: 15px 15px 20px;
}

.user_item {
  display: flex;
  margin-bottom: 15px;
}

.user_img img{
  border-radius: 50%;
  margin-right: 20px;
}

.user_name_city h3{
  font-size: 15px;
  margin-right: 20px;
}

.user_name_city p{
  color: #868585;
  font-size: 14px;
  line-height: 1em;
}
.user_text p{
margin: 0;
padding: 0;
border: 0;
outline: 0;
font-size: 16px;
font-weight: 400;
}

.Feedback_btn {
  position: relative;
  display: block;
  width: 300px;
  height: 72px;
  line-height: 72px;
  letter-spacing: .5px;
  border-radius: 36px;
  margin: 0 auto;
  border: none;
  text-align: center;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  color: #fff;
  background: rgb(226, 0, 21);
  background: -moz-linear-gradient(left, rgba(226, 0, 21, 1) 0%, rgba(252, 1, 24, 1) 100%);
  background: -webkit-linear-gradient(left, rgb(231 27 0) 0%, rgb(252 125 1) 100%);
  background: linear-gradient(to right, rgb(231 27 0) 0%, rgb(252 125 1) 100%);
}

.Feedback_btn p{
  font-size: 20px;
  color: white;
  }

  /* Delivery */
  
  
.Delivery {
    width: 100%;
    max-width: 1920px;
    margin: 0 auto;
    padding: 50px 0;
    background-color: #f4f4f4;
}

 .Delivery h2 {
    margin: 0 0 50px 0;
    text-align: center;
    font-size: 31px;
    color:#c46004;
}

.item {
  background-color: #fff;
  border-radius: 10px;
  margin: 0 10px;
  padding: 30px 20px;
  height: 230px;
}

.item p {    
font-size: 14px;
line-height: 1.4em;
width: 150px;
}

.Delivery_list {
  display: flex;
  justify-content: center;
  align-items: center;
}

.item h3 {
font-size: 16px;
text-transform: none;
margin-top: 10px;
margin-bottom: 10px;
}

.item:nth-child(5) {
  color: #fff;
  height: 250px;
  background: no-repeat 20px 24px, linear-gradient(180deg, rgb(227, 129, 0) 0%, rgb(0 0 0) 100%);
}

/* BuyTools */


.BuyTools {
  height: 700px;
  padding-top: 32px;
  background: url(./img/b1-1.jpg) no-repeat;
}

.buy_title h1 {
  color: #fff;
  text-align: center;
  letter-spacing: 1px;
  font-weight: 400;
  margin-bottom: 10px;
  font-size: 20px;
}

.buy_title span {
  color: #c46004;
}

.buy_title h2 {
  color: #fff;
  font-size: 36px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}

.buy {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.buy img {
  width: 537px;
  height: 560px;
}

.buy_order h3 { 
  font-size: 20px;
  font-weight: 700;
  letter-spacing: .3px;
  padding: 0 45px 0px;
}

.buy_order p { 
  font-size: 17px;
  padding: 0 45px 60px;
  line-height: 1.45em;
}
.buy_order span {
  font-weight: 700;
  padding: 7px 10px 0;
}

.buy_order {
  width: 380px;
  background:#833400;
  border-radius: 5px;
  padding: 20px 0;
  text-align: center;
}

.buy_input {
  display: inline;
}

input::-webkit-input-placeholder       {opacity: 1; transition: opacity 0.3s ease;}
input::-moz-placeholder                {opacity: 1; transition: opacity 0.3s ease;}
input:-moz-placeholder                 {opacity: 1; transition: opacity 0.3s ease;}
input:-ms-input-placeholder            {opacity: 1; transition: opacity 0.3s ease;}
input:focus::-webkit-input-placeholder {opacity: 0; transition: opacity 0.3s ease;}
input:focus::-moz-placeholder          {opacity: 0; transition: opacity 0.3s ease;}
input:focus:-moz-placeholder           {opacity: 0; transition: opacity 0.3s ease;}
input:focus:-ms-input-placeholder      {opacity: 0; transition: opacity 0.3s ease;}

.field {
    outline: 0;
    border: none;
    border-radius: 35px;
    -webkit-border-radius: 35px;
    width: 300px;
    height: 70px;
    line-height: 70px;
    margin: 0 auto 10px;
    padding: 0 20px;
    background: #fff;
    text-align: center;
    font-size: 18px;
}

.buy_order button {
    position: relative;
    display: block;
    width: 300px;
    height: 72px;
    line-height: 72px;
    letter-spacing: .5px;
    border-radius: 36px;
    margin: 0 auto;
    border: none;
    text-align: center;
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    font-size: 20px;
    font-weight: 500;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;
    color: #fff;
    background: rgb(226, 0, 21);
    background: -moz-linear-gradient(left, rgba(226, 0, 21, 1) 0%, rgba(252, 1, 24, 1) 100%);
    background: -webkit-linear-gradient(left, rgb(231 27 0) 0%, rgb(252 125 1) 100%);
    background: linear-gradient(to right, rgb(231 27 0) 0%, rgb(252 125 1) 100%);
}

/* footer */

.footer {
  padding-top: 50px;
  text-align: center;
}

.footer p{
  opacity: 0.7;
}



/* Break ponit */


@media screen and (max-width: 1000px) {
  .header {
    width: 100%;
  }

  .about {
    width: 100%;
    background: no-repeat 20px 24px, linear-gradient(180deg, rgb(0, 0, 0) 0%, rgb(227, 129, 0) 100%);
  }

  .about_wrapper {
    width: 100%;
  }

  .about_wrapper img {
    width: 300px;
    height: 240px;
  }
  .user {
    height: 100%;
  }

  .tools_about {
    width: 90%;
    text-align: center;
    margin: 0 auto 50px;
  }

  .tool {
    width: 100%;
  }

  .items {
    width: 100%;
    justify-content: space-around;
  }

  .items :nth-child(3) {
    display: none;
  }

  .order_tools {
    width: 100%;
  }

  .tbody {
    display: table-row-group;
    vertical-align: middle;
    border-color: inherit;
    width: 100%;
    margin-right: 0px;
  }

  .product_photo img {
    width: 270px;
    height: 252px;
  }

  .user {
    width: 90%;
    height: 137px;
    background-color: #fff;
    margin: 0 auto;
    margin-bottom: 15px;
    border-radius: 5px;
    padding: 15px 15px 20px;
  }

  .Delivery_list :nth-child(2),
  .Delivery_list :nth-child(4),
  .Delivery_list :nth-child(6) {
    display: none;
  }

  .Delivery_list {
    display: grid;
    grid-template-columns: 200px 200px;
    grid-row-gap: 10px;
    grid-column-gap: 10px;
  }

  .item {
    margin: 0px;
  }

  .item:nth-child(5) {
    height: 230px;
  }

  .buy {
    justify-content: none;
  }

  .buy img {
    width: 350px;
    height: 360px;
  }

  .BuyTools {
    background: no-repeat 20px 24px, linear-gradient(180deg, rgb(0, 0, 0) 0%, rgb(227, 129, 0) 100%);
    height: 800px;
  }

  .buy_title {
    margin-bottom: 40px;
  }
}

@media screen and (max-width: 740px) {
  .header li:nth-child(1),
  .header li:nth-child(3){
    display: none;
  }

  .about_wrapper h2 {
    font-size: 24px;
}

.about_btn p{
font-size: 1.6em;
}

.about_prozent {
  width: 300px;
}

.order {
  width: 100%;
}

.tools_title span {
  color: #c46004;
  font-size: 24px;
}

.tools_title {
  font-size: 20px;
  width: 90%;
  margin: 0 auto;
  margin-bottom: 20px;
}

.tools_about {
  width: 70%;
  text-align: center;
  margin: 0 auto 50px;
}
.items {
  display: block;
}

.tools_mus {
  margin: 0 auto;
}

.order_tools {
  display: block;
 text-align: center;
}

.order_list li{
  margin: 0 auto;
  padding: 0 0 25px 0px;
}
.order_tools img {
  width: 300px;
  height: 300px;
}

.included h2 {
  font-size: 24px;
}

.product_title h2 {
  font-size: 24px;
}

.product_list {
  display: block;
  width: 320px;
  margin: 0 auto;
}
.product_photo {
  text-align: center;
}
.product {
  height: 700px;
}
.user {
  height: 100%;
}

.Delivery h2 {
  font-size: 28px;
}
.BuyTools {
  height: 100%;
}

.buy {
  display: block;
  width: 320px;
  margin: 0 auto;
}
.field{
  width: 78%;
}
.buy_order p {
  padding: 0 40px 20px
}

.buy_title h2 {
  font-size: 22px;
}

.buy_order span {
  padding: 0px;
}
.buy_order {
  width: 320px;
}
.buy_order button {
  width: 250px;
  font-size: 16px;
}
.buy img {
  width: 310px;
  height: 300px;
}
}

@media screen and (max-width: 520px) {
  .nav {
    display: none;
  }
  .header {
    justify-content: center;
  }
  .Feedback {
    height: 100%;
    padding-bottom: 20px;
}

.Feedback_title h2 {
  font-size: 24px;
  width: 300px;
  margin:  0 auto 30px;
}
.product_title h2 {
width: 300px;
margin: 0 auto 30px;
}

.tools_title {
  font-size: 18px;
}
.order {
  margin-top: 20px;
}

.Delivery_list {
  display: block;
  width: 240px;
  margin: 0 auto;
}

.item,
.item:nth-child(5) {
  height: 200px;
}
.item {
  margin-bottom: 10px;
}

}